<template>
  <div class="rightContent">
    <div class="navTile">
      当前位置：
      <router-link :to="{name:'Main'}">首页</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'Introduction',query:{typeName,index:0}}">课程介绍</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'IntroductionDetail',query:{typeName,index}}">{{menuName}}</router-link>
    </div>
    <div class="content">
      <div class="myDetail">
        <div id="introduce" class="introduce">
          {{setIntroduce('introduce',file.content)}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import merge from 'webpack-merge'               //用于动态修改地址栏参数的
  import {mapActions} from 'vuex'
  export default {
    name: '',
    data () {
      return {
        List:[],                 //评价数据数组
        menus:[],                //目录数组
        menuName:'',             //菜单名
        typeName:'Introduction', //当前菜单栏选项
        index:0,                 //第几个小菜单

        file:{},                 //当前显示评价
      }
    },
    methods: {
      ...mapActions(['choiceMenuId','choiceTypeMenuId']),

      //显示介绍
      setIntroduce(id,videoIntro){
        if(videoIntro){
          $('#'+id).html(videoIntro)
        }else{
          $('#'+id).html('此栏目下没有数据')
        }
      },

      //修改路由参数
      chanceUrlQuery(data){
        //清空所有的参数：
        this.$router.push({
          query:merge({},{})
        })
        //更新路由参数
        this.$router.push({
          query:merge(this.$route.query,data)
        })
      },

      //更新数据显示
      handleId(){
        var {List,menus,typeName,index} = this
        this.file = List[index]
        this.menuName = menus[index].menuName
        this.choiceMenuId(typeName)
        this.choiceTypeMenuId(typeName+index)
      },
    },
    watch: {
      //深度监听地址栏参数变化，一变化就切换对应的数据
      $route: {
        handler() {
          this.index = this.$route.query.index * 1
          this.handleId()
        },
        deep: true,
      }
    },
    mounted () {
      //页面重新加载时获取地址栏参数显示对应数据
      if(this.$route.query.typeName){
        this.typeName = this.$route.query.typeName
        this.index = this.$route.query.index * 1
      }else{
        var {typeName,index} = this
        this.chanceUrlQuery({typeName,index})
      }
      //当前栏目下的子菜单
      this.menus = [
        {
          typeName:'Introduction',
          menuName:'课程简介',
          name:'IntroductionDetail'
        },
        {
          typeName:'Introduction',
          menuName:'历史沿革',
          name:'IntroductionDetail'
        },
        {
          typeName:'Introduction',
          menuName:'课程特色',
          name:'IntroductionDetail'
        },
        {
          typeName:'Introduction',
          menuName:'课程设计',
          name:'IntroductionDetail'
        },
      ],      //目录
      //当前栏目下的各个菜单包含的数据
      this.List = [
        {
          "id": "0",
          "courseName": "课程简介",
          "content":'本课程是在《动物遗传学》和《生物统计学》课程基础上，在动物科学专业课程体系中起承上启下作用。一方面它与前期所学生物统计学、动物遗传学密切相关，另一方面又是后期动物生产各论课的铺垫。本课程主要内容包括两大部分：选种和选配。通过本课程的学习，使学生掌握家畜育种的基本原理和方法，育种措施的设计和优化，育种技术的新发展及运用和家畜遗传资源的保护与利用等。为学生将来从事动物育种、动物生产和动物保护等相关行业奠定基础。',
        },
        {
          "id": "1",
          "courseName": "历史沿革",
          "content":'（1）杜炳旺（硕导）教授对本课程评价：《家畜育种学》课程注重教学改革，吸收科研成果开设“育种新技术专题讲座”，课程实验课程密切结合生产实际，设计合理，有效地对种用家畜选种方法进行了训练，教学效果较好。\n' +
            '\n' +
            '（2）校内督导贾汝敏教授评价：张丽老师讲课教学态度认真，概念阐述清晰，举例得当，教学内容丰富，注重启发式教学，图片内容丰富，多媒体技术教学手段运用较好，教学效果好。',
        },
        {
          "id": "2",
          "courseName": "课程特色",
          "content":'本课程组建立了《家畜育种学》课程网站，通过该网站学生可以随时学习育种学相关知识，了解育种新技术的发展现状。同时该网站建立了和本课程紧密相关的资源网址连接，例如：家养动物种质资源平台：（http://www.cdad-is.org.cn/），联合国粮农组织家畜多样性信息系统（http://www.fao.org/dad-is）和以动物科学专业为优势学科的美国俄克拉荷马州立大学（Oklahoma State University, http://www.ansi.okstate.edu/breeds）等。通过这些连接可以拓展学习内容，了解国内外家畜育种的动态。\n' +
            '\n' +
            '其次，本课程积累了几届《家畜育种学》教学改革的资料，包括教改论文，教师育种新技术讲座资料和学生育种知识报告资料等，通过这些资料的积累，使得该课程的学习形成了独特的“基本知识讲授+教师育种新技术专题报告+学生育种知识报告”授课和学习模式。\n' +
            '\n' +
            '第三，本课程组教师利用各自主持的科学研究项目来带动学生的学习，使学生参与到教师的科研工作中，进一步从育种学书本理论接触到育种学问题的研究中，提高了学习的兴趣。',
        },
        {
          "id": "3",
          "courseName": "课程设计",
          "content":'',
        },
      ]
      //初始化数据
      this.handleId()
    },
  }
</script>

<style lang="less">
  .userResource{
    .rightContent{
      .navTile{
        padding-left: 40px;
        height: 45px;
        border-radius: 5px 5px 0 0;
        line-height: 45px;
        color: #fff;
        font-size: 14px;
        background: rgb(85,171,235);
        overflow: hidden;
        a{
          color: white;
          text-decoration: none;
          &:hover{
            text-decoration: underline;
          }
        }
      }
      .content{
        line-height: 24px;
        padding: 10px 15px 15px;
        border: 1px solid #CECECE;
        border-top: 0;
        .myDetail{
          min-height: 470px;
          padding-top: 10px;
          .introduce{
            color: #333;
            text-indent: 2em;
            font-size: 14px;
            line-height: 23px;
            min-height: 100px;
          }
        }
      }
    }
  }
  @media screen and (max-width: 767px){
    .userResource{
      .rightContent{
        .navTile{
          padding-left: 10px;
        }
      }
    }
  }
</style>



